.input {
  width: 100%;
  height: 40px;
  padding: 0 18px;
  border: 1px solid #617bb0;
  border-radius: 8px;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: var(--input-text-color);
  outline: none;

  &::placeholder {
    color: var(--input-placeholder-color-main);
    font-size: 10px;
  }
  &:focus,
  &:active {
    outline: none;
    border: 1px solid black;
  }
}

.label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.3em;

  span {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 12px;
  }
}
.message {
  color: red;
  font-size: 0.8rem;
}
